@import url(https://fonts.googleapis.com/css?family=Oswald);
@import url(https://fonts.googleapis.com/css?family=Quattrocento);

.App {
  text-align: center;
  font-family: 'Oswald', sans-serif;
}

/* caption text styling */

.movie-details {
  font-family: 'Quattrocento', Arial, sans-serif;
/*   float: left; */
  overflow: hidden;
/*   margin: 10px 1%; */
/*   width: 100%; */
  color: #141414;
/*   text-align: left; */
}

.movie-details * {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  -webkit-transition: all 0.35s ease;
  -o-transition: all 0.35s ease;
  transition: all 0.35s ease;

}

.movie-details .caption {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-line-pack: justify;
      align-content: space-between;
  position: absolute;
  top: calc(50%);
  height: 80%;
  width: 100%;
  background-color: #000;
  z-index:0;
/*   padding: 5rem 3rem 5rem; */
  -webkit-transform: translateY(20.5%);
      -ms-transform: translateY(20.5%);
          transform: translateY(20.5%);
}

.movie-details .caption:before {
  position: absolute;
  content: '';
  z-index: -1;
  bottom: 100%;
  left: 0;
  width: 100%;
  height: 35%;
  background-image: -webkit-linear-gradient(top, transparent 0%, #000 100%);
  background-image: -webkit-gradient(linear, left top, left bottom, from(transparent), to(#000));
  background-image: -o-linear-gradient(top, transparent 0%, #000 100%);
  background-image: linear-gradient(to bottom, transparent 0%, #000 100%);
}

.movie-details span {
  font-size: .75em;
  -ms-flex-item-align: end;
      align-self: flex-end;
/*   puts the year off the page, we can just not render it though, or format it differently so it just returns the year. */
  -webkit-transform: translate(100%, 100%);
      -ms-transform: translate(100%, 100%);
          transform: translate(100%, 100%)
}

.movie-details h3 {
    -ms-flex-item-align: center;
    align-self: center;
    -webkit-transform: translateY(-80%);
    -ms-transform: translateY(-80%);
    transform: translateY(-25%);
    margin: 0 2vw;
    font-weight: 300;
    padding: 2vw;
    font-size: 6vw;
    line-height: 6vw;
    font-family: 'Oswald', Arial, sans-serif;
    text-transform: uppercase;
    z-index: 3;
    color: #fff;
}

.movie-details p {
    font-family: 'Roboto', sans-serif;
    text-align: left;
    font-weight: 100;
    font-size: 0vw;
    letter-spacing: 1px;
    opacity: 0.9;
    color: #fff;
    -ms-flex-item-align: center;
    align-self: center;
}

.movie-details:hover p {
    -webkit-transform: translateY(33%);
    -ms-transform: translateY(33%);
    transform: translateY(20%);
    height: 45%;
    width: 77%;
    font-size: 3.25vw;
    overflow: scroll;
    -webkit-hyphens: auto;
    -ms-hyphens: auto;
    hyphens: auto;
}

.movie-details:hover .caption,
.movie-details.hover .caption {
  -webkit-transform: translateY(-34%);
      -ms-transform: translateY(-34%);
          transform: translateY(-34%);
  z-index: 2;
}

/* Hex Styling */
.movie-list {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: wrap row;
      flex-flow: wrap row;
  -ms-flex-pack: start;
      justify-content: flex-start;
  height: 100vh;
  overflow: scroll;
  padding: 1rem;
  margin: 1rem;
}

.movie-list__item *{
  position:absolute;
  visibility:visible;
}

/* skewed diamond */

.movie-list li.movie-list__item > section{
  width:100%;
  height:100%;
  text-align:center;
  color:#fff;
  overflow:hidden;

  -webkit-transform: skewY(-30deg) rotate(60deg);
  -ms-transform: skewY(-30deg) rotate(60deg);
  transform: skewY(-30deg) rotate(60deg);

	-webkit-backface-visibility:hidden;

}

/* half hexagon */

.movie-list li.movie-list__item img{
  left:-100%; right:-100%; bottom: -30%;
  width: auto; width:100%;
  margin:0 auto;
}

.movie-list li.movie-list__item:last-of-type{
  bottom: 0;
}



@media (max-width: 450px) {
  .movie-list__item:nth-of-type(2n + 2) {
    right: -33%;
    margin-top: -17%;
    margin-bottom: -17%;
  }
}

.movie-list__item {
  position: relative;
  list-style-type:none;
/*   min-width: 27.85714285714286%; */
  width:65%; /* = (100-2.5) / 3.5 */
  padding-bottom: 75.057736720554273%; /* =  width /0.866 */
  overflow:hidden;
  visibility:hidden;

  -webkit-transform: rotate(-60deg) skewY(30deg);
  -ms-transform: rotate(-60deg) skewY(30deg);
  transform: rotate(-60deg) skewY(30deg);
}

@media (min-width: 450px) {

  .movie-list {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-flow: wrap row;
        flex-flow: wrap row;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    height: 100vh;
    overflow: scroll;
    padding: 1rem;
    margin: 1rem;
  }

  .movie-list__item {
    position: relative;
    list-style-type:none;
    width:50%;
    padding-bottom: 57.774%;
    overflow:hidden;
    visibility:hidden;

    -webkit-transform: rotate(-60deg) skewY(30deg);
    -ms-transform: rotate(-60deg) skewY(30deg);
    transform: rotate(-60deg) skewY(30deg);
  }

  .movie-list__item:nth-of-type(3n + 3) {
      margin: -10vw 21vw;
  }

}



